html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,figcaption,figure,footer,header,hgroup,menu,nav,section,summary,time,mark,audio,video,input{
   font-size:100%;
    font:inherit; 
}

body,html{
    height:100%;
    
}

@mixin flexDisplay{
    display:-moz-box;
    display:-webkit-box;
    display:box;//old
    display:-ms-flexbox;
    display:flexbox; //hybird
    display:-webkit-flex;
    display:flex;
}
@mixin flex{
    -moz-box-flex:1;
    -webkit-box-flex:1;
    box-flex:1;
    -ms-flex:1;
    flex:1;
}
.topbox{
    position: relative;
    overflow: hidden;
    .shadowcover{
        position: absolute;
        left: 0;
        top: 0;
        background:rgba(0,0,0,0.5);
        width: 100%;
        height: 100%;
    }
    img{
        min-width: 100%;
        max-height: 12rem;
    }
    .ftips{
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        @include flexDisplay;
        align-items:center;
        justify-content:center;
        color:#fff;
        font-size: 1.8rem;


    }
}
.wrap{
    width: 100%;
    .topbox{
        width:100%;
        height: 4.4rem;
        line-height: 4.4rem;
    }
}   
@mixin ticon{
        padding-left: 3.5rem;
        background-size: contain;
        background-position: 1.5rem 50%;
        background-repeat: no-repeat;
        width: 1.6rem;
        height: 1.6rem;
        overflow: hidden;

    }
@mixin boxdisplay{
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -moz-box-orient: horizontal;
    -moz-box-direction: normal;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
}
.bookcommont{
    margin-top: 1rem;
    width: 100%;
    height: 4.4rem;
    line-height: 4.4rem;
    border-bottom: 1px solid #ddd;
    a{
        position: relative;
        display: block;
        font-size: 1.4rem;
        @include boxdisplay;
        .bookicon{
            @include ticon;
            background-image: url(http://10.1.16.101:8383/StaticResources/public_html/wings/_resources/img/wx/laikan/backcover1.jpg);
            margin-top: 1.3rem;
        }
        em{
            position: absolute;
            right: 1.5rem;
            top: 0;
            font-style: normal;
            color: #999;
        }
    }
} 

.bookremark{
    margin-top: 1rem;
    width: 100%;
    height: 4.4rem;
    line-height: 4.4rem;
    border-bottom: 1px solid #ddd;
    a{
        position: relative;
        display: block;
        font-size: 1.4rem;
        @include boxdisplay;
        .bookicon{
            @include ticon;
            background-image: url(http://10.1.16.101:8383/StaticResources/public_html/wings/_resources/img/wx/laikan/backcover2.jpg);
            margin-top: 1.3rem;
        }
        em{
            position: absolute;
            right: 1.5rem;
            top: 0;
            font-style: normal;
            color: #999;
        }
    }
} 
.wrecommend{
    width: 100%;
    .wrtitle{
        height: 5rem;
        line-height: 5rem;
        padding: 1.5rem;
        font-size: 1.4rem;
        color: #333;

    }
    .plist{
        width: 100%;
        @include flexDisplay;
        li{
            flex-wrap:wrap;
            //@include flex;
            flex-grow:1;
            a{
                display: block;
                text-align: center;
                padding: 0 1.2rem 1.2rem 1.2rem;
                .tit{
                    display: block;
                    height: 2.2rem;
                    line-height: 2.2rem;
                    font-size: 1.4rem;
                    max-width: 8rem;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    -o-text-overflow: ellipsis;
                    white-space: nowrap;
                }
                img{
                    width: 100%;
                    height: auto;
                    overflow: hidden;
                }
            }
        }
    }
}
.lk-header span, .lk-header span a{
    top: .5rem;
}

@media screen and (min-width: 320px) {
    html {
    font-size:53.3%!important
}
}@media screen and (min-width: 375px) {
    html {
    font-size:62.5%!important
}
}@media screen and (min-width: 414px) {
    html {
    font-size:68.9%!important
}
}@media screen and (min-width: 768px) {
    html {
    font-size:127.9%!important
}
}